<template>
  <div class="id_card_upload">
    <div class="title">淘宝/微信认证</div>
    <van-row class="upload_hint text-desc">
      <van-icon name="hint"/>
      注册流程: 分别打开五张二维码并保存至手机相册，再用手机淘宝or支付宝扫码之后截图上传
    </van-row>
    <van-row v-if="errorMsg" class="upload_hint text-error">
      <van-icon name="hint"/>
      错误提示: {{errorMsg}}
    </van-row>
    <div class="title">淘宝信誉</div>
    <div class="card-item">
      <van-row gutter="20" class="id_card_row upload_box">
        <van-col span=24>
          <div class="upload_wrap" @click="fileSelect(1)">
            <div v-if="xinyu">
              <img :src="xinyu">
            </div>
            <div class="add_btn" v-else>
              <van-icon name="add"/>
              <div>上传照片</div>
            </div>
          </div>
        </van-col>
      </van-row>
      <van-row gutter="10" class="id_card_row">
        <van-col span=12>
          <div class="text-desc">扫描二维码</div>
          <img src="../../../assets/images/123.jpg" width="50%">
        </van-col>
        <van-col span=12>
          <div class="text-desc">示例图</div>
          <img src="../../../assets/images/123.jpg" width="50%">
        </van-col>
      </van-row>
    </div>
    <div class="title">实名认证</div>
    <div class="card-item">
      <van-row gutter="20" class="id_card_row upload_box">
        <van-col span=24>
          <div class="upload_wrap" @click="fileSelect(2)">
            <div v-if="shiming">
              <img :src="shiming">
            </div>
            <div class="add_btn" v-else>
              <van-icon name="add"/>
              <div>上传照片</div>
            </div>
          </div>
        </van-col>
      </van-row>
      <van-row gutter="10" class="id_card_row">
        <van-col span=12>
          <div class="text-desc">扫描二维码</div>
          <img src="../../../assets/images/123.jpg" width="50%">
        </van-col>
        <van-col span=12>
          <div class="text-desc">示例图</div>
          <img src="../../../assets/images/123.jpg" width="50%">
        </van-col>
      </van-row>
    </div>
    <div class="title">花呗</div>
    <div class="card-item">
      <van-row gutter="20" class="id_card_row upload_box">
        <van-col span=24>
          <div class="upload_wrap" @click="fileSelect(3)">
            <div v-if="huabei">
              <img :src="huabei">
            </div>
            <div class="add_btn" v-else>
              <van-icon name="add"/>
              <div>上传照片</div>
            </div>
          </div>
        </van-col>
      </van-row>
      <van-row gutter="10" class="id_card_row">
        <van-col span=12>
          <div class="text-desc">扫码二维码</div>
          <img src="../../../assets/images/123.jpg" width="50%">
        </van-col>
        <van-col span=12>
          <div class="text-desc">示例图</div>
          <img src="../../../assets/images/123.jpg" width="50%">
        </van-col>
      </van-row>
    </div>
    <div class="title">我的淘宝</div>
    <div class="card-item">
      <van-row gutter="20" class="id_card_row upload_box">
        <van-col span=24>
          <div class="upload_wrap" @click="fileSelect(4)">
            <div v-if="wode">
              <img :src="wode">
            </div>
            <div class="add_btn" v-else>
              <van-icon name="add"/>
              <div>上传照片</div>
            </div>
          </div>
        </van-col>
      </van-row>
      <van-row gutter="10" class="id_card_row">
        <van-col span=12>
          <div class="text-desc">扫描二维码</div>
          <img src="../../../assets/images/123.jpg" width="50%">
        </van-col>
        <van-col span=12>
          <div class="text-desc">示例图</div>
          <img src="../../../assets/images/123.jpg" width="50%">
        </van-col>
      </van-row>
    </div>
    <div class="title">微信收款码</div>
    <div class="card-item">
      <van-row gutter="20" class="id_card_row upload_box">
        <van-col span=24>
          <div class="upload_wrap" @click="fileSelect(5)">
            <div v-if="shoukuan">
              <img :src="shoukuan">
            </div>
            <div class="add_btn" v-else>
              <van-icon name="add"/>
              <div>上传照片</div>
            </div>
          </div>
        </van-col>
      </van-row>
      <van-row gutter="10" class="id_card_row">
        <van-col span=12>
          <div class="text-desc">扫描二维码</div>
          <img src="../../../assets/images/123.jpg" width="50%">
        </van-col>
        <van-col span=12>
          <div class="text-desc">示例图</div>
          <img src="../../../assets/images/123.jpg" width="50%">
        </van-col>
      </van-row>
    </div>
    <input @change="fileChanged" type="file" accept="image/jpg, image/jpeg" ref="file" style="display: none"/>

    <div class="register-btn">
      <van-button size="large" @click="next" type="danger">
        提交
      </van-button>
    </div>
  </div>
</template>

<script>
  import { Row, Col, Toast, Dialog, Button } from "vant";
  import { TAOBAO } from "@/api/user";

  export default {

    data() {
      return {
        files: [],
        imgIndex: 0,
        viewOnly: false,
        xinyu: '',
        shiming: '',
        huabei: '',
        wode: '',
        shoukuan: '',
        status:0,
        errorMsg:''
      };
    },

    created(){
      this.initPage();
    },

    methods: {
      initPage(){
        this.$reqGet(TAOBAO).then((res) => {
          this.getSuccess(res);
        }).catch((err) => {
          this.getSuccess(err);
        });
      },
      getSuccess(res){
        if(!res.data){
          return;
        }
        if(res.data.code != 0){
          let msg = res.data.msg ? res.data.msg : "获取认证资料失败";
          Toast.fail(msg);
          return;
        }
        let data = res.data.data;
        this.xinyu = data.xinyu;
        this.shiming = data.shiming;
        this.huabei = data.huabei;
        this.wode = data.wode;
        this.shoukuan = data.shoukuan;
        this.status = data.status;
        this.errorMsg = data.desc;
      },
      fileSelect(i) {
        this.imgIndex = i;
        this.$refs.file && this.$refs.file.click();
      },
      fileChanged(e) {
        let list = this.$refs.file.files;
        if (this.disabled || !list.length) {
          return;
        }
        let f = list[0];
        if (!this.isContain(f)) {
          const item = {
            name: f.name,
            size: f.size,
            src: "",
            file: f
          };
          this.html5Reader(f, item);
          this.files.push(item);
        }
        this.$refs.file.value = "";
      },
      // 将图片文件转成BASE64格式
      html5Reader(file, item) {
        const reader = new FileReader();
        reader.onload = (e) => {
          item.src = e.target.result;
          switch (this.imgIndex){
            case 1: {
              this.xinyu = item.src;
              break;
            }
            case 2: {
              this.shiming = item.src;
              break;
            }
            case 3: {
              this.huabei = item.src;
              break;
            }
            case 4: {
              this.wode = item.src;
              break;
            }
            case 5: {
              this.shoukuan = item.src;
              break;
            }
          }
        };
        reader.readAsDataURL(file);
      },

      isContain(file) {
        return this.files.find((item) => item.name === file.name && item.size === file.size);
      },

      next() {
        if (!this.files || this.files.length < 5) {
          Dialog.alert({
            title: "警告",
            message: "请上传全部认证图片"
          }).then(() => {
            // on close
          });
          return;
        }

        let formdata = new FormData();
        formdata.append("xinyu", this.files[0].file);
        formdata.append("shiming", this.files[1].file);
        formdata.append("huabei", this.files[2].file);
        formdata.append("wode", this.files[3].file);
        formdata.append("shoukuan", this.files[4].file);

        this.$reqPost(TAOBAO, formdata).then((res) => {
          this.submitSuccess(res);
        }).catch((err) => {
          this.submitSuccess(err);
        });
      },

      submitSuccess(res) {
        let data = res.data;
        if(!data){
          return;
        }
        if (data.code != 0) {
          let msg = (data && data.msg) ? data.msg : "资料提交失败";
          Toast.fail(msg);
          return;
        }
        Dialog.alert({
          title: "资料提交成功",
          message: "请等待平台审核，认证资料审核成功后才能领取任务"
        }).then(() => {
          this.$router.go(-1);
        });
      }
    },

    components: {
      [Row.name]: Row,
      [Col.name]: Col,
      [Toast.name]: Toast,
      [Dialog.name]: Dialog,
      [Button.name]: Button
    }
  };
</script>

<style>
  .van-cell__value {
    overflow: hidden;
    text-align: left;
    vertical-align: middle;
  }
</style>

<style lang="scss" scoped>

  .card-item{
    padding: 10px;
    background-color: #f2f2f2;
  }
  .id_card_upload {
    background-color: #fff;
    padding: 15px 0px;
    > div {
      margin-bottom: 15px;
    }
  }
  .title{
    margin-left: 10px;
  }
  .upload_hint {
    margin-left: 10px;
    position: relative;
    padding-left: 20px;
    line-height: 1.6;
    i {
      position: absolute;
      top: 2px;
      left: 0;
    }
  }

  .id_card_row {
    > div {
      text-align: center;
    }
    .text-desc {
      margin-top: 8px;
      margin-bottom: 8px;
    }
    .text-error {
      margin-top: 8px;
      margin-bottom: 8px;
      color:red;
    }
  }

  .upload_box .upload_wrap {
    position: relative;
    border: 1px dashed $gray;
    min-height: 100px;
    box-sizing: border-box;
    padding: 5px;
    text-align: center;
    img {
      max-width: 100%;
      height: 120px;
    }
    .add_btn {
      position: absolute;
      top: 50%;
      left: 50%;
      font-size: 16px;
      color: $gray;
      transform: translate(-50%, -50%);
      i {
        font-size: 24px;
      }
    }
  }

  .register-btn{
    padding: 0px 10px;
  }

</style>



